import React from 'react'
import { useRouteNode, useRoute } from 'react-router5'
import {
	View,
	Root
} from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css'

import Payment from './panels/Payment'
import Contacts from './panels/Contacts'
import ContactsAdd from './panels/ContactsAdd'
import Methods from './panels/Methods'
import MethodsAdd from './panels/MethodsAdd'
import Successful from './panels/Successful'

export default function App () {
	const { router } = useRoute()
	const { route } = useRouteNode('')
	const topRouteName = route.name.split('.')[0]
	const tabRouteName = route.name.split('.')[1]

	function navigate (path, opts) {
		router.navigate(path, {}, opts)
	}

	return (
		<Root activeView={topRouteName === 'Successful' ? 'Successful' : 'Main'}>
			<View id="Main" activePanel={topRouteName === 'Successful' ? 'Payment' : topRouteName}>

			 <Payment id="Payment" navigate={navigate} />

			 <Contacts id="Contacts" navigate={navigate} />

			 <ContactsAdd id="ContactsAdd"navigate={navigate} />

			 <Methods id="Methods"navigate={navigate} />

			 <MethodsAdd id="MethodsAdd" tab={tabRouteName} avigate={navigate} />

			</View>
			<View id="Successful" activePanel="Successful_Main">

			 <Successful id="Successful_Main" navigate={navigate} />
			 
			</View>
		</Root>
	)
}

