import React from 'react'
import { useRouteNode } from 'react-router5'
import {
	Panel,
	PanelHeader,
	PanelHeaderButton,
	Tabs,
	TabsItem
} from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css'
import IconBack from '@vkontakte/icons/dist/28/chevron_back'

import GroupAddCard from '../components/GroupAddCard'
import GroupAddBank from '../components/GroupAddBank'

export default function App ({ id, navigate }) {
	const { route } = useRouteNode('MethodsAdd')
	const tabRouteName = route.name.split('.')[1]

	return (
		<Panel id={id}>
			<PanelHeader left={<PanelHeaderButton onClick={() => window.history.back()}><IconBack/></PanelHeaderButton>}>
				Add Payment Method
			</PanelHeader>
			<Tabs>
				<TabsItem selected={tabRouteName === 'Card'}
									onClick={() => navigate('MethodsAdd.Card', {replace: true})}>
					Card
				</TabsItem>
				<TabsItem selected={tabRouteName === 'Bank'}
									onClick={() => navigate('MethodsAdd.Bank', {replace: true})}>
					Bank Account
				</TabsItem>
			</Tabs>
			{tabRouteName === 'Card' ? (
				<GroupAddCard onSubmit={() => navigate('Payment')} />
			) : tabRouteName === 'Bank' ? (
				<GroupAddBank onSubmit={() => navigate('Payment')} />
			) : null}
		</Panel>
	)
}

