import React, { Component } from 'react'
import { connect } from 'react-redux'
import { addMethod, setMethod } from '../store'
import {
  Button,
  Div,
  FormLayout,
  Group,
  Input
} from '@vkontakte/vkui'
import Inputmask from 'inputmask'

export class GroupAddCard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      fullName: '',
      cardNumber: '',
      expiryDate: '',
      errors: {}
    }
  }

  componentDidMount () {
    let cardNumber = document.querySelector("[name='cardNumber']")
    new Inputmask("9999-9999-9999-9999").mask(cardNumber)

    let expiryDate = document.querySelector("[name='expiryDate']")
    new Inputmask("99/99").mask(expiryDate)
  }

  onChange(e) {
    const { errors } = this.state;
    const { name, value, inputmask } = e.currentTarget;
    if (inputmask) {
      this.setState({ [name]: inputmask.unmaskedvalue() });
    } else {
      this.setState({ [name]: value });
    }
    if (errors[name]) {
      delete errors[name]
      this.setState({ errors });
    }
  }

  submit () {
    const { fullName, cardNumber, expiryDate } = this.state;
    let { errors } = this.state;
    const { onSubmit, addMethod, setMethod } = this.props

    if (!fullName) {
      errors = { ...errors, fullName: 'Field is required' }
    }

    if (!cardNumber) {
      errors = { ...errors, cardNumber: 'Field is required' }
    } else if (cardNumber.length < 19) {
      errors = { ...errors, cardNumber: 'Card Number is not valid' }
    }

    if (!expiryDate) {
      errors = { ...errors, expiryDate: 'Field is required' }
    } else if (expiryDate.length < 5) {
      errors = { ...errors, expiryDate: 'Expiry Date is not valid' }
    }

    this.setState({ errors });

    if (Object.keys(errors).length === 0) {
      const state = { ...this.state }
      state.type = 'card'
      delete state.errors
      addMethod(state)
      setTimeout(() => {
        const { methods } = this.props
        setMethod(methods[methods.length - 1])
        onSubmit()
      }, 0)
    }
  }

  render () {
    const { fullName, cardNumber, expiryDate, errors } = this.state
    const { id } = this.props

    return (
      <Group id={id}>
        <FormLayout>
          <Input top="Full Name"
                 value={fullName}
                 name="fullName"
                 status={errors.fullName ? 'error' : undefined}
                 bottom={errors.fullName}
                 onChange={(e) => this.onChange(e)} />
          <Input top="Card Number"
                 value={cardNumber}
                 name="cardNumber"
                 status={errors.cardNumber ? 'error' : undefined}
                 bottom={errors.cardNumber}
                 onChange={(e) => this.onChange(e)} />
          <Input top="Expiry Date"
                 value={expiryDate}
                 name="expiryDate"
                 status={errors.expiryDate ? 'error' : undefined}
                 bottom={errors.expiryDate}
                 onChange={(e) => this.onChange(e)} />
        </FormLayout>
        <Div>
          <Button stretched size="l" onClick={() => this.submit()}>Save</Button>
        </Div>
      </Group>
    )
  }
}

const mapStateToProps = (state) => {
  const { methods } = state
  return { methods }
}
const mapDispatchToProps = { addMethod, setMethod }

export default connect(mapStateToProps, mapDispatchToProps)(GroupAddCard)
