import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setMethod, setContact } from '../store'
import {
	Panel,
	PanelHeader,
	Placeholder,
	Button
} from '@vkontakte/vkui'
import IconCheck from '@vkontakte/icons/dist/56/check_circle_outline'

export class Successful extends Component {
	constructor(props) {
		super(props)
		this.state = {}
	}

	newPayment () {
		const { setContact, setMethod, navigate } = this.props
		setContact(null)
		setMethod(null)
		navigate('Payment')
	}

	render () {
		const { id } = this.props
		return (
			<Panel id={id}>
				<PanelHeader>Successful</PanelHeader>
				<Placeholder icon={<IconCheck />}
										 action={<Button size="l"
																		 mode="tertiary"
																		 onClick={() => this.newPayment()}>Make Another One</Button>}
										 stretched>
					Payment completed<br/>
					successfully
				</Placeholder>
			</Panel>
		)
	}
}

const mapDispatchToProps = { setContact, setMethod }

export default connect(null, mapDispatchToProps)(Successful)
