import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setMethod } from '../store'
import {
	Panel,
	PanelHeader,
	PanelHeaderButton,
	SimpleCell,
	Div,
	Avatar,
	Caption,
	Card,
	Group,
	Header
} from '@vkontakte/vkui'
import IconAdd from '@vkontakte/icons/dist/28/add_circle_outline'
import IconBack from '@vkontakte/icons/dist/28/chevron_back'
import IconCard from '@vkontakte/icons/dist/28/payment_card_outline'

export class Methods extends Component {
	constructor(props) {
		super(props)
		this.state = {}
	}

	setMethod (method) {
		const { setMethod, navigate } = this.props
		setMethod(method)
		navigate('Payment')
	}

	Card (method, i) {
		return (
			<Card size="l"
						key={'card_' + i}
						onClick={() => this.setMethod(method)}>
				<SimpleCell>
					<Avatar style={{ background: 'var(--accent)', marginBottom: 16 }}
									size={28}
									shadow={false}>
						<IconCard width={20} height={20} fill="var(--white)" />
					</Avatar>
					{method.fullName}
					<Caption level="2" style={{ marginTop: 10, color: 'var(--text_secondary)', display: 'flex', justifyContent: 'space-between' }}>
						<span>{'**** ' + method.cardNumber.slice(-4)}</span>
						<span>{method.expiryDate}</span>
					</Caption>
				</SimpleCell>
			</Card>
		)
	}

	Bank (method, i) {
		return (
			<Card size="l"
						key={'bank_' + i}
						onClick={() => this.setMethod(method)}>
				<SimpleCell>
					{method.accountName}
					<Caption level="2" style={{ marginTop: 10, color: 'var(--text_secondary)', display: 'flex', justifyContent: 'space-between' }}>
						<span>{method.accountNumber}</span>
						<span>{method.bsb}</span>
					</Caption>
				</SimpleCell>
			</Card>
		)
	}

	render () {
		const { id, navigate, methods } = this.props
		return (
			<Panel id={id}>
				<PanelHeader left={<PanelHeaderButton onClick={() => window.history.back()}><IconBack/></PanelHeaderButton>}
										 right={<PanelHeaderButton onClick={() => navigate('MethodsAdd.Card')}><IconAdd/></PanelHeaderButton>}>
					Select Method
				</PanelHeader>
				<Group header={<Header mode="secondary">Credits Cards</Header>}>
					<Div>
						{methods.map((method, i) => { if (method.type === 'card') return this.Card(method, i) })}
					</Div>
				</Group>
				<Group header={<Header mode="secondary">Bank Accounts</Header>}>
					<Div>
						{methods.map((method, i) => { if (method.type === 'bank') return this.Bank(method, i) })}
					</Div>
				</Group>
			</Panel>
		)
	}
}

const mapStateToProps = (state) => {
	const { methods } = state
	return { methods }
}
const mapDispatchToProps = { setMethod }

export default connect(mapStateToProps, mapDispatchToProps)(Methods)
