import React, { Component } from 'react'
import { connect } from 'react-redux'
import {
	Panel,
	PanelHeader,
	FormLayout,
	Div,
	Input,
	SelectMimicry,
	Button
} from '@vkontakte/vkui'
import Inputmask from "inputmask";

export class Payment extends Component {
	constructor(props) {
		super(props)
		this.state = {
			amount: '',
			errors: {}
		}
	}

	componentDidMount () {
		let selector = document.querySelector("[name='amount']")
		let im = new Inputmask({ regex: "\\d*" }).mask(selector)
		im.mask(selector);
	}

	submit () {
		const { navigate, contact, method } = this.props
		const { amount } = this.state
		let { errors } = this.state

		if (!amount) {
			errors = { ...errors, amount: 'Field is required' }
		}

		if (!contact) {
			errors = { ...errors, contact: 'Field is required' }
		}

		if (!method) {
			errors = { ...errors, method: 'Field is required' }
		}

		this.setState({ errors });

		if (Object.keys(errors).length === 0) {
			navigate('Successful')
		}
	}

	onChange(e) {
		const { errors } = this.state;
		const { name, value } = e.currentTarget;
		this.setState({ [name]: value });
		if (errors[name]) {
			delete errors[name]
			this.setState({ errors });
		}
	}

	render () {
		const { id, navigate, contact, method } = this.props
		const { errors, amount } = this.state

		return (
			<Panel id={id}>
				<PanelHeader>Make a Payment</PanelHeader>
				<FormLayout>
					<SelectMimicry top="Select Contact"
												 placeholder="Press to select"
												 status={errors.contact ? 'error' : undefined}
												 bottom={errors.contact}
												 onClick={() => navigate('Contacts')}>
						{contact ? contact.firstName + ' ' + contact.lastName : undefined}
					</SelectMimicry>
					<SelectMimicry top="Select Payment Method"
												 placeholder="Press to select"
												 status={errors.method ? 'error' : undefined}
												 bottom={errors.method}
												 onClick={() => navigate('Methods')}>
						{method ? method.type === 'card' ? method.fullName : method.accountName : undefined}
					</SelectMimicry>
					<Input top="Enter Amount"
								 name="amount"
								 value={amount}
								 status={errors.amount ? 'error' : undefined}
								 bottom={errors.amount}
								 onChange={(e) => this.onChange(e)}/>
				</FormLayout>
				<Div>
					<Button stretched size="l" onClick={() => this.submit()}>Pay</Button>
				</Div>
			</Panel>
		)
	}
}

const mapStateToProps = (state) => {
	const { contact, method } = state
	return { contact, method }
}

export default connect(mapStateToProps)(Payment)
