import React, { Component } from 'react'
import { connect } from 'react-redux'
import { addMethod, setMethod } from '../store'
import {
  Button,
  Div,
  FormLayout,
  Group,
  Input
} from '@vkontakte/vkui'
import Inputmask from 'inputmask'

export class GroupAddBank extends Component {
  constructor(props) {
    super(props)
    this.state = {
      accountName: '',
      accountNumber: '',
      bsb: '',
      errors: {}
    }
  }

  componentDidMount () {
    let bsb = document.querySelector("[name='bsb']")
    new Inputmask("999-999").mask(bsb)
  }

  onChange(e) {
    const { errors } = this.state;
    const { name, value } = e.currentTarget;
    this.setState({ [name]: value });
    if (errors[name]) {
      delete errors[name]
      this.setState({ errors });
    }
  }

  submit () {
    const { accountName, accountNumber, bsb } = this.state;
    let { errors } = this.state;
    const { onSubmit, addMethod, setMethod } = this.props

    if (!accountName) {
      errors = { ...errors, accountName: 'Field is required' }
    }

    if (!accountNumber) {
      errors = { ...errors, accountNumber: 'Field is required' }
    } else if (accountNumber.length < 4) {
      errors = { ...errors, accountNumber: 'Account Number is not valid' }
    }

    if (!bsb) {
      errors = { ...errors, bsb: 'Field is required' }
    }

    this.setState({ errors });

    if (Object.keys(errors).length === 0) {
      const state = { ...this.state }
      state.type = 'bank'
      delete state.errors
      addMethod(state)
      setTimeout(() => {
        const { methods } = this.props
        setMethod(methods[methods.length - 1])
        onSubmit()
      }, 0)
    }
  }

  render () {
    const { accountName, accountNumber, bsb, errors } = this.state
    const { id } = this.props

    return (
      <Group id={id}>
        <FormLayout>
          <Input top="Account Name"
                 value={accountName}
                 name="accountName"
                 status={errors.accountName ? 'error' : undefined}
                 bottom={errors.accountName}
                 onChange={(e) => this.onChange(e)} />
          <Input top="Account Number"
                 value={accountNumber}
                 name="accountNumber"
                 status={errors.accountNumber ? 'error' : undefined}
                 bottom={errors.accountNumber}
                 onChange={(e) => this.onChange(e)} />
          <Input top="BsB"
                 value={bsb}
                 name="bsb"
                 status={errors.bsb ? 'error' : undefined}
                 bottom={errors.bsb}
                 onChange={(e) => this.onChange(e)} />
        </FormLayout>
        <Div>
          <Button stretched size="l" onClick={() => this.submit()}>Save</Button>
        </Div>
      </Group>
    )
  }
}

const mapStateToProps = (state) => {
  const { methods } = state
  return { methods }
}
const mapDispatchToProps = { addMethod, setMethod }

export default connect(mapStateToProps, mapDispatchToProps)(GroupAddBank)
