import { combineReducers } from 'redux';

export const ADD_CONTACT = 'contacts/add_contact'
export const SET_CONTACT = 'contacts/set_contact'
export const ADD_METHOD = 'methods/add_method'
export const SET_METHOD = 'methods/set_method'

const INITIAL_CONTACTS = []
const INITIAL_METHODS = []
const INITIAL_CONTACT = null
const INITIAL_METHOD = null

function contacts(state = INITIAL_CONTACTS, action){
  switch (action.type) {
    case ADD_CONTACT:
      return [
        ...state,
        { ...action.payload.data }
      ]
    default:
      return state
  }
}

function methods(state = INITIAL_METHODS, action){
  switch (action.type) {
    case ADD_METHOD:
      return [
        ...state,
        { ...action.payload.data }
      ]
    default:
      return state
  }
}

function contact(state = INITIAL_CONTACT, action){
  switch (action.type) {
    case SET_CONTACT:
      return action.payload.data
    default:
      return state
  }
}

function method(state = INITIAL_METHOD, action){
  switch (action.type) {
    case SET_METHOD:
      return action.payload.data
    default:
      return state
  }
}

const reducers = combineReducers({
  contacts,
  methods,
  contact,
  method
});

export default reducers

export function addContact(data) {
  return {
    type: ADD_CONTACT,
    payload: { data }
  }
}

export function addMethod(data) {
  return {
    type: ADD_METHOD,
    payload: { data }
  }
}

export function setContact(data) {
  return {
    type: SET_CONTACT,
    payload: { data }
  }
}

export function setMethod(data) {
  return {
    type: SET_METHOD,
    payload: { data }
  }
}
