import React, { Component } from 'react'
import { connect } from 'react-redux'
import { addContact, setContact } from '../store'
import {
	Button,
	Div,
	FormLayout,
	Input,
	Panel,
	PanelHeader,
	PanelHeaderButton
} from '@vkontakte/vkui'
import IconBack from '@vkontakte/icons/dist/28/chevron_back'

function validateEmail (email) {
	let re = /\S+@\S+\.\S+/;
	return re.test(email);
}

export class ContactsAdd extends Component {
	constructor(props) {
		super(props)
		this.state = {
			firstName: '',
			lastName: '',
			email: '',
			errors: {}
		}
	}

	onChange(e) {
		const { errors } = this.state;
		const { name, value } = e.currentTarget;
		this.setState({ [name]: value });
		if (errors[name]) {
			delete errors[name]
			this.setState({ errors });
		}
	}

	submit () {
		const { firstName, lastName, email } = this.state;
		let { errors } = this.state;
		const { navigate, addContact, setContact } = this.props

		if (!firstName) {
			errors = { ...errors, firstName: 'Field is required' }
		}

		if (!lastName) {
			errors = { ...errors, lastName: 'Field is required' }
		}

		if (!email) {
			errors = { ...errors, email: 'Field is required' }
		} else if (!validateEmail(email)) {
			errors = { ...errors, email: 'Email is not valid' }
		}

		this.setState({ errors });

		if (Object.keys(errors).length === 0) {
			const state = { ...this.state }
			delete state.errors
			addContact(state)
			setTimeout(() => {
				const { contacts } = this.props
				setContact(contacts[contacts.length - 1])
				navigate('Payment')
			}, 0)
		}
	}

	render () {
		const { firstName, lastName, email, errors } = this.state;
		const { id } = this.props

		return (
			<Panel id={id}>
				<PanelHeader left={<PanelHeaderButton onClick={() => window.history.back()}><IconBack/></PanelHeaderButton>}>
					Add New Contact
				</PanelHeader>
				<FormLayout>
					<Input top="First Name"
								 value={firstName}
								 name="firstName"
								 status={errors.firstName ? 'error' : undefined}
								 bottom={errors.firstName}
								 onChange={(e) => this.onChange(e)} />
					<Input top="Last Name"
								 value={lastName}
								 name="lastName"
								 status={errors.lastName ? 'error' : undefined}
								 bottom={errors.lastName}
								 onChange={(e) => this.onChange(e)} />
					<Input top="Email"
								 value={email}
								 name="email"
								 status={errors.email ? 'error' : undefined}
								 bottom={errors.email}
								 onChange={(e) => this.onChange(e)} />
				</FormLayout>
				<Div>
					<Button stretched size="l" onClick={() => this.submit()}>Save</Button>
				</Div>
			</Panel>
		)
	}
}

const mapStateToProps = (state) => {
	const { contacts } = state
	return { contacts }
}
const mapDispatchToProps = { addContact, setContact }

export default connect(mapStateToProps, mapDispatchToProps)(ContactsAdd)
