import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setContact } from '../store'
import {
	Panel,
	PanelHeader,
	PanelHeaderButton,
	Group,
	SimpleCell
} from '@vkontakte/vkui'
import IconAdd from '@vkontakte/icons/dist/28/add_circle_outline'
import IconBack from '@vkontakte/icons/dist/28/chevron_back'

export class Contacts extends Component {
	constructor(props) {
		super(props)
		this.state = {}
	}

	setContact (contact) {
		const { setContact, navigate } = this.props
		setContact(contact)
		navigate('Payment')
	}

	render () {
		const { id, navigate, contacts } = this.props
		return (
			<Panel id={id}>
				<PanelHeader left={<PanelHeaderButton onClick={() => window.history.back()}><IconBack/></PanelHeaderButton>}
										 right={<PanelHeaderButton onClick={() => navigate('ContactsAdd')}><IconAdd/></PanelHeaderButton>}>
					Select Contact
				</PanelHeader>
				<Group>
					{contacts.map((contact, i) => (
						<SimpleCell description={contact.email}
												key={'contact_' + i}
												onClick={() => this.setContact(contact)}>
							{contact.firstName} {contact.lastName}
						</SimpleCell>
					))}
				</Group>
			</Panel>
		)
	}
}

const mapStateToProps = (state) => {
	const { contacts } = state
	return { contacts }
}
const mapDispatchToProps = { setContact }

export default connect(mapStateToProps, mapDispatchToProps)(Contacts)
